"use client";

import { useState, useEffect, useRef } from "react";
import SearchBox from "@/components/SearchBox";
import AnimeCard from "@/components/AnimeCard"; // novo componente para cada anime
import { useAnimeList } from "@/hooks/useAnimeList";

export default function Page() {
  const { items, search, sortItems, loadMore, finished, loading } = useAnimeList();
  const [term, setTerm] = useState("");
  const containerRef = useRef<HTMLDivElement>(null);

  // Scroll infinito
  const handleScroll = () => {
    if (!containerRef.current || loading || finished) return;

    const { scrollTop, scrollHeight, clientHeight } = containerRef.current;
    if (scrollTop + clientHeight >= scrollHeight - 150) {
      loadMore(term);
    }
  };

  useEffect(() => {
    const div = containerRef.current;
    div?.addEventListener("scroll", handleScroll);
    return () => div?.removeEventListener("scroll", handleScroll);
  }, [term, loading, finished]);

  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        flexDirection: "column",
        background: "#121212",
        color: "#fff",
        padding: "20px",
      }}
    >
      {/* Título */}
      <h1 style={{ fontSize: "2rem", marginBottom: "20px", textAlign: "center" }}>
        Explorar Animes
      </h1>

      {/* Busca e Ordenação */}
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          gap: "10px",
          maxWidth: "600px",
          margin: "0 auto 20px auto",
        }}
      >
        <SearchBox
          value={term}
          onChange={(v) => setTerm(v)}
          onSearch={() => search(term)}
        />

        <div style={{ display: "flex", gap: "10px" }}>
          <button
            style={{
              flex: 1,
              padding: "10px",
              borderRadius: "8px",
              background: "#1f1f1f",
              color: "#fff",
              cursor: "pointer",
            }}
            onClick={() => sortItems("nome")}
          >
            Ordenar por Nome
          </button>

          <button
            style={{
              flex: 1,
              padding: "10px",
              borderRadius: "8px",
              background: "#1f1f1f",
              color: "#fff",
              cursor: "pointer",
            }}
            onClick={() => sortItems("ano")}
          >
            Ordenar por Ano
          </button>
        </div>
      </div>

      {/* Lista de Animes */}
      <div
        ref={containerRef}
        style={{
          flex: 1,
          overflowY: "auto",
          display: "grid",
          gridTemplateColumns: "repeat(auto-fill, minmax(180px, 1fr))",
          gap: "15px",
          padding: "10px",
        }}
      >
        {items.map((anime) => (
          <AnimeCard key={anime.mal_id} anime={anime} />
        ))}

        {loading && (
          <div style={{ gridColumn: "1/-1", textAlign: "center" }}>Carregando...</div>
        )}

        {finished && items.length > 0 && (
          <div style={{ gridColumn: "1/-1", textAlign: "center", color: "#888" }}>
            Fim da lista
          </div>
        )}

        {!loading && items.length === 0 && (
          <div style={{ gridColumn: "1/-1", textAlign: "center", color: "#888" }}>
            Nenhum anime encontrado
          </div>
        )}
      </div>
    </div>
  );
}
