export default function AnimeCard({ anime }) {
  return (
    <div style={{
      border: "1px solid #ccc",
      borderRadius: 8,
      padding: 10,
      display: "flex",
      gap: 10,
      marginBottom: 12
    }}>
      <img
        src={anime.images?.jpg?.image_url}
        alt={anime.title}
        width={70}
        height={100}
        style={{ objectFit: "cover", borderRadius: 4 }}
      />

      <div>
        <h3 style={{ margin: 0 }}>{anime.title}</h3>
        {anime.year && <p style={{ margin: "4px 0" }}>Ano: {anime.year}</p>}
      </div>
    </div>
  );
}
